import * as React from 'react';
import { Plugin } from '@ali/4ever-cangjie';
import { ReferPlugin } from '@ali/4ever-bamboo';
import { ReferPluginConfig } from './types';

export default function createRenderNode(config: ReferPluginConfig): Plugin['renderNode'] {
  const { previewDingTalkFile } = config;
  return {
    refer: (props, _) => {
      return <ReferPlugin.Refer {...props} onDingpanPreview={previewDingTalkFile} />;
    },
  };
}
